@import './media-query-style.scss';

#main {


    .page-bottom-flex {
        z-index: 99;
        position: relative;
    }

    .gradiant-text {


        * {
            background: -webkit-linear-gradient(0deg, #0182F9 0%, #FFFFFF 70%);

            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        strong {
            font-weight: 700;
        }

    }

    #main-video {
        transition: 0.6s all;
        // opacity: 0;
        z-index: 2;
        left: 50%;
        transform: translate(-50%);
    }
}