.gradiant-text {


    * {
        line-height: 36px;
        font-weight: 200;
        font-size: 26px;

    }

    strong {
        font-weight: 500;
    }
}

.smallDelay {
    animation-delay: 0.5s;
    animation-duration: 0.5s;
}


.video-preview {
    margin: 0px;
    transition: 0.3s all;
    cursor: pointer;
    padding: 9px;

    &-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .frame {

        position: relative;
        padding: 15px;

        &-inner {
            padding: 7px;

            &-content {
                overflow: hidden;
                padding: 0px;
                width: 350px;
                height: 250px;
                filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

                .play {
                    position: absolute;
                    width: 45px;
                    height: 45px;
                    object-fit: contain;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 20;
                    transition: 0.2s all;
                    transform-origin: center;
                }

                &:hover {
                    .play {
                        transform: scale(1.1) translate(-50%, -50%);
                    }
                }
            }
        }
    }

}


.page-bottom-flex {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
    margin-top: auto;
    padding-right: 260px;
    padding-bottom: 80px;
    padding-left: 80px;

    .frame {
        margin: 0px;
    }
}

.full-screen-video {
    position: absolute;
    left: 0px;
    top: 0px;

}

@media (min-aspect-ratio: 16/9) {
    .full-screen-video {
        width: 100%;
        height: auto;
    }
}

@media (max-aspect-ratio: 16/9) {
    .full-screen-video {
        width: auto;
        height: 100%;
    }
}


@media only screen and (max-width: $mobile) {
    .gradiant-text {
        * {
            line-height: 24px;
            font-weight: 200;
            font-size: 16px;

        }


    }

    .video-preview {
        max-width: calc(100% - 100px);
        max-height: 200px;


        .frame-inner-content {
            width: 100%;
            height: 100%;
        }
    }

    .page-bottom-flex {
        flex-direction: column;
        padding: 0px;
        justify-content: flex-start;
        align-items: center;
        margin-top: 50px;
        flex: 1;
        padding-bottom: 60px;

        .frame {
            height: fit-content;
        }

        .gradiant-text {
            order: 2;
            text-align: center;
            margin-top: auto;
        }
    }
}