#species {
    .subtitle {
        max-width: 630px;
    }

    .title {
        margin-bottom: 13px;

    }

    .swiper {
        width: 1215px;
        padding-right: 75px;
        margin: 0 auto;
        height: 490px;
        margin-top: 60px;
        position: relative;
        z-index: 99;

        &-wrapper {
            align-items: center;
        }

        &-slide {
            height: 263px;
            cursor: pointer;
            transition: 0.2s all;

            .frame {
                width: 200px;
                padding: 13px;
                transition: 0.4s all;

                &-inner {
                    padding: 6px;

                    &-content {
                        background: rgba(0, 0, 0, 0.5);
                    }
                }
            }

            .species-slider-element {
                width: 162px;

                &-text {
                    position: absolute;
                    bottom: -15px;
                    width: auto;
                    height: 64px;
                    background-position: top;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    &-overlay {
                        position: absolute;
                        background: radial-gradient(62.06% 71.01% at 50% 50%, rgba(1, 130, 249, 0.5) 0%, rgba(0, 0, 0, 0) 100%, rgba(1, 130, 249, 0) 100%);
                        width: 100%;
                        height: 100%;
                        filter: blur(10px);
                    }

                    p {
                        position: relative;
                        color: rgba(255, 255, 255, 0.8);
                        font-size: 16px;
                        line-height: 34px;
                        text-align: center;

                        font-family: $main-font;
                    }

                }

                &-globe {
                    position: absolute;
                    top: -50px;
                    width: 55px;
                    height: 55px;


                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: contain;
                    }
                }

                &-avatar {
                    width: auto;
                    height: 159px;
                    object-fit: contain;
                    position: relative;
                    bottom: 10px;
                }
            }



            &-active {
                width: 294px !important;

                .species-slider-element {
                    &-text {


                        bottom: -22px;

                        p {
                            background: linear-gradient(325.91deg, #0182F9 14.14%, rgba(1, 130, 249, 0) 126.27%);
                            filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.25));
                            color: white;
                            font-weight: 600;
                            padding: 0px 20px;
                        }
                    }

                    &-avatar {
                        height: 180px;
                        bottom: 0px;
                    }
                }

                .frame {
                    transform: scale(1.47);
                    -webkit-transform: scale(1.47);
                    transform-origin: center;
                }

            }
        }

    }

    .species {
        &-slider-element {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
        }




        &-bottom-text {
            margin-top: auto;
            max-width: 440px;
            font-size: 16px;
            font-weight: 200;
            line-height: 24px;
            text-align: center;
            margin-bottom: 40px;

            strong {
                font-weight: 600;
            }
        }







    }
}


@media only screen and (max-width: 1550px) {

    #species {
        .swiper {
            zoom: 0.75;
        }
    }
}

@media only screen and (max-width: 1350px) {

    #species {
        .swiper {
            zoom: 0.7;
        }
    }
}

@media only screen and (max-width: $mobile) {

    #species {
        .swiper {

            zoom: 0.6;
            position: relative;
            left: 50%;
            transform: translate(-50%)
        }

        .species {


            &-bottom-text {
                font-size: 14px;
                line-height: 20px;
                padding-left: 20px;
                padding-right: 20px;
            }
        }
    }
}